@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

:root{--primaryColor: #148fdb;--backgroundColor: rgb(23, 2, 66)}*{font-family:"Noto Sans JP", sans-serif}.button{display:inline-block;cursor:pointer;margin:10px 12px;padding:12px 16px;border-radius:12px;border:solid #148fdb 2px;border:solid var(--primaryColor) 2px}.button.primary{color:#148fdb;color:var(--primaryColor);background-color:white;border:5px solid #148fdb;border:5px solid var(--primaryColor);font-weight:bold}.button.secondary{background-color:white;color:#148fdb;color:var(--primaryColor)}.action{display:inline-block;padding:0 18px}.dialog{background-color:white;color:rgb(23, 2, 66);color:var(--backgroundColor);border-radius:24px;border:solid #148fdb 6px;border:solid var(--primaryColor) 6px;padding:12px 24px;max-width:800px;text-align:center}.dialog .title{color:#148fdb;color:var(--primaryColor);font-weight:bold;text-align:center;font-size:1.25em;margin:6px 0}.animated-in .content{animation:fadeIn 0.3s ease-out both}.animated-in .content:nth-child(1){animation-delay:.12s}.animated-in .content:nth-child(2){animation-delay:.24s}.animated-in .content:nth-child(3){animation-delay:.36s}.animated-in .content:nth-child(4){animation-delay:.48s}@keyframes fadeIn{0%{opacity:0;transform:translateY(20px)}100%{opacity:1}}

:root{--text: #2B3044;--line: #BBC1E1;--line-active: #275EFE}p{font-size:18px;margin:0;color:#2B3044;color:var(--text)}p a{display:inline-block;position:relative;text-decoration:none;color:inherit;margin:0 0px;margin:0 var(--spacing, 0px);transition:margin .25s}p a svg{width:76px;height:40px;position:absolute;left:50%;bottom:0;transform:translate(-50%, 7px) translateZ(0);fill:none;stroke:var(--line);stroke:var(--stroke, var(--line));stroke-linecap:round;stroke-width:2px;stroke-dasharray:69px 278px;stroke-dasharray:var(--offset, 69px) 278px;stroke-dashoffset:361px;transition:stroke 0.25s ease 0s,stroke-dasharray 0.35s;transition:stroke 0.25s ease var(--stroke-delay, 0s),stroke-dasharray 0.35s}p a:hover{--spacing: 4px;--stroke: var(--line-active);--stroke-delay: .1s;--offset: 180px}.dribbble{position:fixed;display:block;right:20px;bottom:20px}.dribbble img{display:block;height:28px}

*{box-sizing:border-box}.blog-card{display:flex;flex-direction:column;margin:1rem auto;box-shadow:0 3px 7px -1px #000;margin-bottom:1.6%;background:#fff;line-height:1.4;font-family:sans-serif;border-radius:5px;overflow:hidden;z-index:0}.blog-card a{color:inherit}.blog-card a:hover{color:#148fdb}.blog-card:hover .photo{transform:scale(1.3) rotate(3deg)}.blog-card .meta{position:relative;z-index:0;height:200px}.blog-card .photo{position:absolute;top:0;right:0;bottom:0;left:0;background-size:cover;background-position:center;transition:transform .2s}.blog-card .details,.blog-card .details ul{margin:auto;padding:0;list-style:none}.blog-card .details{position:absolute;top:0;bottom:0;left:-100%;margin:auto;transition:left .2s;background:rgba(0,0,0,0.6);color:#fff;padding:10px;width:100%;font-size:1.9rem}.blog-card .details a{-webkit-text-decoration:dotted underline;text-decoration:dotted underline}.blog-card .details ul li{display:inline-block}.blog-card .details .author:before{font-family:FontAwesome;margin-right:10px;content:"\f007"}.blog-card .details .date:before{font-family:FontAwesome;margin-right:10px;content:"\f133"}.blog-card .details .tags ul:before{font-family:FontAwesome;content:"\f02b";margin-right:10px}.blog-card .details .tags li{margin-right:2px}.blog-card .details .tags li:first-child{margin-left:-4px}.blog-card .description{padding:1rem;background:#fff;position:relative;z-index:1}.blog-card .description h1,.blog-card .description h2{color:#000;font-family:Poppins, sans-serif}.blog-card .description h1{line-height:1;margin:0;font-size:1.7rem}.blog-card .description h2{font-size:1rem;font-weight:300;text-transform:uppercase;color:#a2a2a2;margin-top:5px}.blog-card .description .technologies{text-align:left;display:flex;margin-top:10px;align-items:baseline;flex-wrap:wrap}.blog-card .description .read-more{text-align:right;display:flex;justify-content:flex-end}.blog-card .description .read-more a{color:#148fdb;position:relative;padding-left:30px}.blog-card .description .read-more a:after{content:"\f061";font-family:FontAwesome;margin-left:-10px;opacity:0;vertical-align:middle;transition:margin .3s, opacity .3s}.blog-card .description .read-more a:hover:after{opacity:1}.blog-card p{color:#000;position:relative;margin:1rem 0 0}.blog-card p:first-of-type{margin-top:1.25rem}.blog-card p:first-of-type:before{content:"";position:absolute;height:5px;background:#148fdb;width:35px;top:-0.75rem;border-radius:3px}.blog-card:hover .details{left:0%}@media (min-width: 640px){.blog-card{flex-direction:row;max-width:980px;height:300px}.blog-card .meta{flex-basis:40%;height:auto}.blog-card .description{flex-basis:60%}.blog-card .description:before{transform:skewX(-3deg);content:"";background:#fff;width:30px;position:absolute;left:-10px;top:0;bottom:0;z-index:-1}.blog-card.alt{flex-direction:row-reverse}.blog-card.alt .description:before{left:inherit;right:-10px;transform:skew(3deg)}.blog-card.alt .details{padding-left:25px}}

:root{--canvasColor: #f9f9f9}*{box-sizing:border-box}strong{font-weight:500}em{font-style:italic}h1{font:700 48px/1.2 "Poppins", sans-serif;margin-bottom:10px}h2{font:700 32px/1.2 "Poppins", sans-serif;margin-bottom:10px}h3{font:700 24px/1.2 "Poppins", sans-serif;margin-bottom:10px}h4{font:700 20px/1.2 "Poppins", sans-serif;margin-bottom:10px}h5{font:500 18px/1.2 "Poppins", sans-serif;margin-bottom:10px}h6{font:500 16px/1.2 "Poppins", sans-serif;text-transform:uppercase}.wrapper{display:flex;justify-content:center;flex-wrap:wrap}.profile{position:relative;background:#C7D3D4FF;color:#00203FFF;box-shadow:0 3px 15px rgba(51,51,51,0.2);border-radius:10px;transition:.2s ease}.profile__image img{width:100%;height:100%;object-fit:cover}.profile__stats__title{color:var(--grayColor);text-transform:uppercase;font-size:16px}.profile a:hover{top:0;box-shadow:none;background:var(--primaryShade4)}.profile a:active,.profile a:focus{top:0;box-shadow:none;background:var(--primaryShade5)}.profile-smallimg{width:300px;height:500px;margin:20px;border-radius:10px;text-align:center;display:grid;grid-template-columns:1fr;grid-gap:10px;text-align:center}.profile-smallimg .profile__image{margin-top:-40px}.profile-smallimg .profile__image img{background-color:#fff;width:100px;height:100px;border-radius:50%;border:5px solid var(--backgroundColor);box-shadow:0 0 0 7px var(--secondaryColor)}.profile-smallimg .profile__info{margin:10px 30px 15px}.profile-smallimg .profile__stats{margin:0 30px 10px}.profile-smallimg .profile__stats:nth-of-type(n+4){display:none}.profile-smallimg .profile__cta a{border-radius:0 0 10px 10px}

